import React from 'react';
import { Button } from 'react-bootstrap';
import './foras-verifymenu.scss';
import userApi from '../../services/user.service';
import session from '../../services/session.service';
import helper from '../../services/helper.service';

class ForasVerifyMenu extends React.Component {
    constructor() {
        super();
    }
    sendemail = () => {
        helper.startLoader();
        userApi.verifyemail()
            .then((response) => {
                helper.stopLoader();
                if (response.status === 200) {
                    // session.set('foras-user', response.data.data);
                    helper.showToast('Success', 'Successfully Email Verify', 1);
                    window.location.reload();
                } else if(response.status === 400){
                    if(response.errors) {
                        this.setState({errors: response.errors});
                    }
                } else {
                    helper.showToast('Error', 'Invalid User', 3);
                }
            })
            .catch((error) => {
                helper.stopLoader();
                console.log('err: ', error);
                helper.showToast('Error', 'Invalid User', 3);
            });
    }
    sendphone = () => {
        helper.startLoader();
        userApi.verifyphone()
            .then((response) => {
                helper.stopLoader();
                if (response.status === 200) {
                    // session.set('foras-user', response.data);
                    helper.showToast('Success', 'Successfully Phone Verify', 1);
                    window.location.reload();
                } else if(response.status === 400){
                    if(response.errors) {
                        this.setState({errors: response.errors});
                    }
                } else {
                    helper.showToast('Error', 'Invalid User', 3);
                }
            })
            .catch((error) => {
                helper.stopLoader();
                console.log('err: ', error);
                helper.showToast('Error', 'Invalid User', 3);
            });
    }
    render() {
        return (
            <div className="row">
                <p className="mtop">{this.props.title}</p>
                <div className="btn-send" onClick={this.props.type == 'email' ? this.sendemail: this.sendphone}>
                    {this.props.btnname}
                </div>
            </div>
        );
    }
}
export default ForasVerifyMenu;