import React from 'react';
import { GiftedChat } from 'react-web-gifted-chat';
import './footer.scss';
import firebaseSvc from '../../../services/FirebaseSvc';
import session from '../../../services/session.service';

const img_btn_go = require('../../../assets/images/btn-go.png');
const profile = require('../../../assets/profile-images/corporate.png');

class Footer extends React.Component{
    
    constructor(props) {
      super();
      this.state = {
        isLoggedIn: false,
        messages: [],
        role : null
      };
    }

    // get user() {
      
    //   const user = session.get('foras-user');
    //   return {
    //       id: this.props.msgdata.sender_id+'_'+this.props.msgdata.receiver_id+'_'+this.props.msgdata.job_id,
    //       name: user.name,
    //       email:user.email,
    //       _id: firebaseSvc.uid, // need for gifted-chat
    //   };
    // }
      
    componentDidMount() {
        const user = session.get('foras-user');
        const isLoggedIn = user ? true : false;
        if(user){
          this.setState({
            isLoggedIn: isLoggedIn,
            role: user.role
          });
        }       
        
        // firebaseSvc.refOn(message =>
        //   this.setState(previousState => ({
        //       messages: GiftedChat.append(previousState.messages, message),
        //   }))
        // );
    }
    
    componentWillMount() {
        firebaseSvc.refOff();
    }

    componentWillReceiveProps( nextProps) {
      
      const user = session.get('foras-user');

      if (nextProps.msgdata!=this.props.msgdata) {

        console.log("nextProps===>",nextProps.msgdata);
        
        this.state.messages=[];
        this.setState({messages: []});

        // firebaseSvc.ref.orderByChild('receiver_id').equalTo(user.id).on("child_added", function(snapshot) {
        firebaseSvc.ref.on("child_added", function(snapshot) {
          // console.log(snapshot.child('sender_id').val(),'<====>',nextProps.msgdata.sender_id);
          
          if(snapshot.child('sender_id').val() == nextProps.msgdata.sender_id && snapshot.child('receiver_id').val() == user.api_token || snapshot.child('receiver_id').val() == nextProps.msgdata.sender_id && snapshot.child('sender_id').val() == user.api_token ){                 
            this.state.messages.unshift(snapshot.val());
          }         
          this.setState({messages: this.state.messages});
          }, this);
      }

    }
    
    sendMsg = (msg) => {
        console.log(msg);
        console.log(this.state);
        const sender = {name : session.get('foras-user').name, api_token : session.get('foras-user').api_token, logo : session.get('foras-user').logo};
        let message = [];
        this.setState({messages: message});
        msg.map((obj, index) =>
            {
              message = [{
                sender_id: sender.api_token,
                receiver_id: this.props.msgdata.sender_id,
                // message: this.state.message,
                text: obj.text,
                message: obj.text,
                subject: 'real message',
                sender: sender,
                id: this.props.msgdata.sender_id,
                createdAt: new Date(),
                user: {
                    id: sender.api_token,
                    name: sender.name,
                    avatar: sender.logo ? sender.logo : profile,
                }
              }];                           
            }            
        );
        console.log('receiver id===>',message); 
        firebaseSvc.send(message);
        // this.setState({
        //   messages: []
        // });
    }

    award = () => {
      const sender = {name : session.get('foras-user').name, api_token : session.get('foras-user').api_token, logo : session.get('foras-user').logo};
      let message = [];
  
      message = [{
        sender_id: sender.api_token,
        receiver_id: this.props.msgdata.sender_id,
        // message: this.state.message,
        text: 'you are awarded',
        message: 'you are awarded',
        subject: 'award message',
        sender: sender,
        id: this.props.msgdata.sender_id,
        createdAt: new Date(),
        user: {
            id: sender.api_token,
            name: sender.name,
            avatar: sender.logo ? sender.logo : profile,
        }
      }];
      firebaseSvc.send(message);
      // this.setState((previousState) => ({
      //   messages: GiftedChat.append(previousState.messages, message),
      // }));
    }

    accept = () => {
      const sender = {name : session.get('foras-user').name, api_token : session.get('foras-user').api_token, logo : session.get('foras-user').logo};
      let message = [];
      message = [{
        sender_id: sender.api_token,
        receiver_id: this.props.msgdata.sender_id,
        // message: this.state.message,
        text: 'thanks you i accepted',
        message: 'thanks you i accepted',
        subject: 'accept message',
        sender: sender,
        id: this.props.msgdata.sender_id,
        createdAt: new Date(),
        user: {
            id: sender.api_token,
            name: sender.name,
            avatar: sender.logo ? sender.logo : profile,
        }
      }];
      firebaseSvc.send(message);
      // this.setState((previousState) => ({
      //   messages: GiftedChat.append(previousState.messages, message),
      // }));
    }

    get user() {
      const sender = session.get('foras-user');
      const senduser = {
        id: sender.api_token,
        name: sender.name,
        avatar: sender.logo ? sender.logo : profile,
      }
      return senduser;
    }

    render() {
        return (
            <div className="footer">
                <div className="container">                   
                    <span> حقوق النشر محفوظة - فرصنا - 2019 </span>
                </div>
                {this.state.isLoggedIn == true && this.props.show ?
                <div className="chatpan">
                  <div className="job_btn" >
                      <button class="btn btn-primary close" onClick={this.props.handleHide}> x</button>
                      {this.state.role === 'individual' && <button class="btn btn-primary" onClick={this.accept}> accept</button>}
                      {this.state.role === 'corporate' && <button class="btn btn-primary" onClick={this.award}> award</button>}
                      
                  </div>
                    <GiftedChat
                      messages={this.state.messages}
                      onSend={this.sendMsg}
                      user={this.user}
                    />
                </div> : '' }
            </div>
        )
    }
}

export default Footer;