import React from 'react';
import ImageUploader from 'react-images-upload';
import userApi from '../../services/user.service';
import 'react-rater/lib/react-rater.css';
import session from '../../services/session.service';
import helper from '../../services/helper.service';
import { Button, InputGroup, FormControl, Form } from 'react-bootstrap';
import './profile-edit-header.scss';

const detail_img = require('../../assets/profile-images/default-individual.png');
const icon_camera = require('../../assets/icons/icon-camera.png');

const img_btn_go = require('../../assets/images/btn-go.png');
const icon_user = require('../../assets/icons/icon-user.png');
const icon_location = require('../../assets/icons/icon-location.png');
const icon_tel = require('../../assets/icons/icon-tel.png');
const icon_cv = require('../../assets/icons/icon-cv.png');
const icon_chart = require('../../assets/icons/icon-chart.png');

class ProfileEditHeader extends React.Component {
    constructor(props) {
		super(props);
		 this.state = { 
                role: null,
                user: {},
                name: '',
                work_area: '',
                hourly_rate: '',
                city: '',
                available_work_from_time: '',
                experience: '',
                languages: '',
                skills: '',
                about_me: '',
                pictures: [ ],
                logo:[ ],
                errors: {}
            };
		 this.onDrop = this.onDrop.bind(this);
	}

	onDrop(picture, src) {
		this.setState({
            pictures: this.state.pictures.concat(picture),
            logo : src
        });
    }
    
    handleInputChange = (event) => {
        const target = event.target;
        const name = target.name;
        switch(target.type) {
            case 'button' : 
                this.setState({[name] : target.value});
                break;
            case 'checkbox' : 
                break;
            default : 
                this.setState({[name] : target.value});
                break;
        }
    }

    editIndividualProfile = () => {
        userApi.updateProfile(this.state)
            .then((response) => {
                if (response.status === 200) {
                    session.set('foras-user', response.data);
                    helper.showToast('Success', 'Successfully logged in', 1);
                    window.location.reload();
                } else if(response.status === 400){
                    if(response.errors) {
                        this.setState({errors: response.errors});
                    }
                } else {
                    helper.showToast('Error', 'Invalid User', 3);
                }
            })
            .catch((error) => {
                console.log('err: ', error);
                helper.showToast('Error', 'Invalid User', 3);
            });
        if(this.state.pictures.length != 0){
        userApi.uploadProfile(this.state.pictures)
            .then((response) => {
                if (response.status === 200) {
                    session.set('foras-user', response.data.data);
                    helper.showToast('Success', 'Successfully logged in', 1);
                    window.location.reload();
                } else if(response.status === 400){
                    if(response.errors) {
                        this.setState({errors: response.errors});
                    }
                } else {
                    helper.showToast('Error', 'Invalid User', 3);
                }
            })
            .catch((error) => {
                console.log('err: ', error);
                helper.showToast('Error', 'Invalid User', 3);
            });
        }
    }
    
    componentDidMount() {
        const user = session.get('foras-user');
        if(user) {
            this.setState({role: user.role, user: user, logo : user.logo});
        } else {
            window.location.href='/';
        }
    }
    render() {
        return (
            <div className="container pt-3">
                <div className="card individual-detail-header text-center">
                    <div className="row">
                        <div className="col-4 package-info">
                        
                        </div>
                        <div className="col-4 profile-info">
                            <div className="detail-img-container">
                                <div className="detail-img-wrapper p-0">
                                    <img src={this.state.logo 
                                        ? this.state.logo 
                                        : detail_img} alt="detail-img" className="detail-img" />
                                </div>
                            </div>
                            <ImageUploader
                                        withPreview={false}
                                        withIcon={false}
                                        withLabel={false}
                                        buttonText='Choose images'
                                        onChange={this.onDrop}
                                        imgExtension={['.jpg', '.gif', '.png', '.gif']}
                                        maxFileSize={5242880}
                                    />
                        </div>
                        <div className="col-4 package-info">
                            
                        </div>
                    </div>
                    <Form className="register-form">
                        <div className="row">
                            <div className="col-6">
                                <InputGroup className="d-block">
                                    <img src={icon_user} className="input-icon" alt="User Icon" />
                                    <FormControl
                                        placeholder={this.props.user.name == null ? 'Name' : this.props.user.name}
                                        aria-label="Name"
                                        aria-describedby="basic-addon1"
                                        type="text"
                                        name="name"
                                        value={this.state.name}
                                        onChange={this.handleInputChange}
                                    />
                                    <div className="err-msg"> {this.state.errors.name ? this.state.errors.name[0] : ''} </div>
                                </InputGroup>
                                <InputGroup className="d-block">
                                    <img src={icon_location} className="input-icon" alt="Location Icon" />
                                    <FormControl
                                        placeholder={this.props.user.work_area == null ? 'Work Area' : this.props.user.work_area}
                                        aria-label="Work Area"
                                        aria-describedby="basic-addon1"
                                        type="text"
                                        name="work_area"
                                        value={this.state.work_area}
                                        onChange={this.handleInputChange}
                                    />
                                    <div className="err-msg"> {this.state.errors.name ? this.state.errors.name[0] : ''} </div>
                                </InputGroup>
                                <InputGroup className="d-block">
                                    <img src={icon_location} className="input-icon" alt="Location Icon" />
                                    <FormControl
                                        placeholder={this.props.user.city == null ? 'City' : this.props.user.city}
                                        aria-label="City"
                                        aria-describedby="basic-addon1"
                                        type="text"
                                        name="city"
                                        value={this.state.city}
                                        onChange={this.handleInputChange}
                                    />
                                    <div className="err-msg"> {this.state.errors.name ? this.state.errors.name[0] : ''} </div>
                                </InputGroup>
                                {this.props.user.role === 'individual' ?
                                <InputGroup className="d-block">
                                    <img src={icon_chart} className="input-icon" alt="Chart Icon" />
                                    <FormControl
                                        placeholder={this.props.user.hourly_rate == null ? 'Hourly Rate' : this.props.user.hourly_rate}
                                        aria-label="Hourly Rate"
                                        aria-describedby="basic-addon1"
                                        type="text"
                                        name="hourly_rate"
                                        value={this.state.hourly_rate}
                                        onChange={this.handleInputChange}
                                    />
                                    <div className="err-msg"> {this.state.errors.name ? this.state.errors.name[0] : ''} </div>
                                </InputGroup>:''}
                            </div>
                            <div className="col-6 right-border-indicator">
                            {this.props.user.role === 'individual' ?
                                <InputGroup className="d-block">
                                    <img src={icon_chart} className="input-icon" alt="Chart Icon" />
                                    <FormControl
                                        placeholder={this.props.user.available_work_from_time == null ? 'Available Work From Time' : this.props.user.available_work_from_time}
                                        aria-label="Available Work From Time"
                                        aria-describedby="basic-addon1"
                                        type="text"
                                        name="available_work_from_time"
                                        value={this.state.available_work_from_time}
                                        onChange={this.handleInputChange}
                                    />
                                    <div className="err-msg"> {this.state.errors.name ? this.state.errors.name[0] : ''} </div>
                                </InputGroup>:''}
                                {this.props.user.role === 'individual' ?
                                <InputGroup className="d-block">
                                    <img src={icon_chart} className="input-icon" alt="Chart Icon" />
                                    <FormControl
                                        placeholder={this.props.user.experience == null ? 'Experience' : this.props.user.experience}
                                        aria-label="Experience"
                                        aria-describedby="basic-addon1"
                                        type="text"
                                        name="experience"
                                        value={this.state.experience}
                                        onChange={this.handleInputChange}
                                    />
                                    <div className="err-msg"> {this.state.errors.name ? this.state.errors.name[0] : ''} </div>
                                </InputGroup>:''}
                                {this.props.user.role === 'individual' ?
                                <InputGroup className="d-block">
                                    <img src={icon_chart} className="input-icon" alt="Chart Icon" />
                                    <FormControl
                                        placeholder={this.props.user.languages == null ? 'Languages' : this.props.user.languages}
                                        aria-label="Languages"
                                        aria-describedby="basic-addon1"
                                        type="text"
                                        name="languages"
                                        value={this.state.languages}
                                        onChange={this.handleInputChange}
                                    />                                    
                                    <div className="err-msg"> {this.state.errors.name ? this.state.errors.name[0] : ''} </div>
                                </InputGroup>
                                :''}
                                {this.props.user.role === 'individual' ?
                                <InputGroup className="d-block">
                                    <img src={icon_chart} className="input-icon" alt="Chart Icon" />
                                    <FormControl
                                        placeholder={this.props.user.skills == null ? 'Skills' : this.props.user.skills}
                                        aria-label="Skills"
                                        aria-describedby="basic-addon1"
                                        type="text"
                                        name="skills"
                                        value={this.state.skills}
                                        onChange={this.handleInputChange}
                                    />
                                    <div className="err-msg"> {this.state.errors.name ? this.state.errors.name[0] : ''} </div>
                                </InputGroup>
                                :''}
                            </div>
                            <div className="col-12">
                                <InputGroup className="my-3 d-block">
                                    <FormControl
                                        placeholder={this.props.user.about_me == null ? 'About Me' : this.props.user.about_me}
                                        aria-label="about_me"
                                        aria-describedby="basic-addon1"
                                        as="textarea" 
                                        rows="5"
                                        name="about_me"
                                        value={this.state.about_me}
                                        onChange={this.handleInputChange}
                                    />
                                    <div className="err-msg"> {this.state.errors.message ? this.state.errors.message[0] : ''} </div>
                                </InputGroup>
                                {this.props.user.role === 'individual' ?
                                <Button className="btn-go icon-button" onClick={this.editIndividualProfile}>
                                    <img src={img_btn_go} className="img-btn-go" alt="Go Button" />
                                </Button>
                                :
                                <Button className="btn-go icon-button" onClick={this.editIndividualProfile}>
                                    <img src={img_btn_go} className="img-btn-go" alt="Go Button" />
                                </Button>}
                            </div>
                        </div>
                    </Form>
                </div>
            </div>
        );
    }
    
}

export default ProfileEditHeader;