import axios from 'axios'
import http from './http.service';
import session from './session.service';
import environment from '../environments';

const path = {
    register: 'web/register',
    verifyemail: 'auth/verify/email',
    verifyphone: 'auth/verify/phone',
    login: 'auth/login',
    socialLogin: 'web/login/socials',
    getIndividuals: 'users/individuals',
    getCorporates: 'users/corporates',
    individualDetail: 'users/individuals',
    corporateDetail: 'users/corporates',
    getReviews: 'reviews',
    updateProfile: 'users/profile/update',
    uploadProfile: 'users/profile/upload',
    setReviews: 'reviews/create'
};

export default {
    register: function(user) {
        return http.post(path.register, user, {});
    },
    verifyemail: function() {
        const user = session.get('foras-user');
        const api_token = user ? user.api_token : '';
        const header = {
            Authorization: 'Bearer ' + api_token
        };
        return http.post(path.verifyemail, user, header);
    },
    verifyphone: function(user) {
        return http.post(path.verifyphone, user, {});
    },
    login: function(user) {
        return http.post(path.login, user, {});
    },
    socialLogin: function(profileObj) {
        return http.post(path.socialLogin, profileObj, {});
    },
    getIndividuals: function(body) {
        const user = session.get('foras-user');
        const api_token = user ? user.api_token : '';
        const header = {
            Authorization: 'Bearer ' + api_token
        };

        return http.post(path.getIndividuals, body, header);
    },
    getCorporates: function(body) {
        const user = session.get('foras-user');
        const api_token = user ? user.api_token : '';
        const header = {
            Authorization: 'Bearer ' + api_token
        };

        return http.post(path.getCorporates, body, header);
    },
    individualDetail: function(param) {
        const user = session.get('foras-user');
        const api_token = user ? user.api_token : '';
        const header = {
            Authorization: 'Bearer ' + api_token
        };

        return http.get(`${path.individualDetail}/${param}`, header);
    },
    corporateDetail: function(param) {
        const user = session.get('foras-user');
        const api_token = user ? user.api_token : '';
        const header = {
            Authorization: 'Bearer ' + api_token
        };

        return http.get(`${path.corporateDetail}/${param}`, header);
    },
    getReviews: function() {
        const user = session.get('foras-user');
        const api_token = user ? user.api_token : '';
        const header = {
            Authorization: 'Bearer ' + api_token
        };

        return http.get(`${path.getReviews}`, header);
    },
    setReviews: function(body) {
        const user = session.get('foras-user');
        const api_token = user ? user.api_token : '';
        const header = {
            Authorization: 'Bearer ' + api_token
        };

        return http.post(`${path.setReviews}`, body ,header);
    },
    updateProfile: function(body) {
        const user = session.get('foras-user');
        const api_token = user ? user.api_token : '';
        const header = {
            Authorization: 'Bearer ' + api_token
        };

        return http.post(`${path.updateProfile}`, body ,header);
    },
    uploadProfile: function(file) {
        const user = session.get('foras-user');
        const api_token = user ? user.api_token : '';
        const header = {
            headers : {Authorization: 'Bearer ' + api_token,
            'content-type': 'multipart/form-data'}
        };

        let formData = new FormData();    //formdata object

        formData.append('logo', file[0]);
        formData.append('_method', 'PUT');

        return axios.post(`${environment.baseApi}/${path.uploadProfile}`, formData, header);
            // .then(response => {
            //     console.log(response);
            // })
            // .catch(error => {
            //     console.log(error);
            // });

        // return http.post(`${path.uploadProfile}`, formData ,header);
    }
}